.page {
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
}

.page__body {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding-inline: 16px;
  transition-duration: .3s;
  display: flex;
}

.product-card__image {
  object-position: center;
  margin-inline: 3px;
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.product-card__title {
  text-align: left;
  color: #060b35;
  height: 36px;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.product-card__code {
  font-size: 10px;
  font-weight: 400px;
  color: #616070;
  align-self: start;
  width: 98px;
  height: 14px;
  margin-top: 4px;
  margin-bottom: 0;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: end;
  height: 16px;
  margin-top: 16px;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.product-card__price-name {
  color: #616070;
  width: 31px;
  height: 18px;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.product-card__price-value {
  width: 50px;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button-buy {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  transition-duration: .3s;
}

.product-card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.reviews {
  color: #060b35;
  margin: 0;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.main {
  --column-count: 1;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}
